@font-face {
  font-family: "HakgyoansimSantteutdotumL";
  src: url("/src/fonts/HakgyoansimSantteutdotumL.ttf") format("truetype");
}

.user_index {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative; /* position: relative 추가 */
  z-index: 1; /* z-index를 낮춰서 UserNav 뒤에 위치하도록 설정 */
}

.user_index_text {
  font-family: "HakgyoansimSantteutdotumL";
  text-align: center;
  display: flex;
  flex-direction: column;
  opacity: 0;
  transform: translateY(50px);
  animation: fadeInUp 1s ease-out forwards;
  animation-delay: 0.5s;
}

.user_index_text h1 {
  font-family: "HakgyoansimSantteutdotumL";
  font-size: 500%;
  font-style: italic;
}

.user_index_section1 {
  width: 100%;
  display: flex;
  flex-direction: row;
  opacity: 0;
  transform: translateY(50px);
  animation: fadeInUp 1s ease-out forwards;
  animation-delay: 0.2s;
}

.user_index_section1 img {
  width: 50%;
  height: 400px;
}

.user_index_section1_text {
  width: 50%;
  text-align: center;
  display: flex;
  flex-direction: column;
  font-family: "HakgyoansimSantteutdotumL";
  justify-content: center;
}

.user_index_section2 {
  width: 100%;
  display: flex;
  flex-direction: row;
  opacity: 0;
  transform: translateY(50px);
  animation: fadeInUp 1s ease-out forwards;
  animation-delay: 0.8s;
}

.user_index_section2_text {
  width: 50%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.user_index_section2 img {
  height: 400px;
  width: 50%;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
